interface state {
  /** 讀取狀態 */
  loading: boolean;
  /** 後端回傳的站台資料 */
  // branch: typeInfo.response
  branch: {};
  /** 可否註冊 */
  register: boolean;
  /** 註冊驗證方式 */
  // validType: typeInfo.validType
  validType: string;
  /** 站台狀態 */
  enabled: boolean;
  /** logo */
  logo: string;
  /** 站台的主題 */
  theme: string | null;
  /** 主題未設定 */
  themeError: boolean;
  /** 站台出現大型錯誤 */
  branchErrorCode?: number | string;
  /** 必填通訊軟體種類 */
  commSetting: {
    Line?: commSetting;
    Skype?: commSetting;
    Zalo?: commSetting;
    Telegram?: commSetting;
    Wechat?: commSetting;
  };
  /** 該站台可選擇的銀行 */
  availableBanks: { id: number; name: string; code: string }[];
}

/** 通訊軟體的設定值 */
type commSetting = {
  /** 通訊軟體的id，也是編輯時的index */
  id: number;
  /** 是否為必填 */
  required: boolean;
  /** 通訊軟體的code */
  code: string;
};

