<template>
  <router-view />
</template>

<script lang='ts'>
import { computed } from "vue";
import { useStore } from "vuex";
import initDevice from "./API/initDevice";

/**
 * 基底層
 */
export default {
  name: "APP",
  setup() {
    initDevice();

    const $store = useStore();
    return {
      width: computed(() => $store.state.device.screenWidth),
    };
  },
};
</script>

