import { AxiosResponse } from "axios";
import $http from "../../../axios"; // 後端溝通

const API = "casinoPaymentFlow/front-collectSequence/info";

export namespace typeInfo {
  // export interface request {  }
  export interface AxiosResponse extends _AxiosResponse {}
  export interface response {
    p27_collect_sequence: p27_collect_sequence[];
  }

  /** 整理過後的物件 */
  export type filtedItem = {
    id: number;
    method: string;
    label: string;
    sort: number;
  };

  export type p27_collect_sequence = {
    /** 金流商號通道ID */
    p27_cse_p23_bmd_id: number;
    /** 順序 */
    p27_cse_sequence: number;
    /** 商號通道 string number 1 2 4 8 16 32 */
    p23_bmd_method: "1" | "2" | "4" | "8" | "16" | "32";
    /** 單筆上限 null,整數 null則無上限 */
    p23_bmd_singular_upper_limit: string | null;
    /** 單筆下限 null,整數 null則無上限 */
    p23_bmd_singular_lower_limit: string | null;
  };
}
interface _AxiosResponse extends AxiosResponse<responseData> {}

interface responseData extends backendResponse {
  response: { p27_collect_sequence: typeInfo.p27_collect_sequence[] };
}

/**
 * 取得儲值資訊
 * http://redmine.mirfak.tw/projects/pkg-270000-casinopaymentflow/repository/55/revisions/master/entry/_md/api/Front/CollectSequence/%E8%B3%87%E8%A8%8A.md
 */
export default () => {
  return $http(API).then((d: typeInfo.AxiosResponse) => {
    if (d.data.code !== 0) {
      throw d.data.code;
    }
    return d;
  });
};

export function sorting(
  A: typeInfo.p27_collect_sequence,
  B: typeInfo.p27_collect_sequence,
) {
  return A.p27_cse_sequence - B.p27_cse_sequence;
}

/**
 * 整理出可用的唯一通道做輸出
 */
export function setMethods(items: typeInfo.p27_collect_sequence[]) {
  const sorted = items.sort(sorting);
  const PAY_METHODS = ["1", "2", " 4", "8", "16", "32"];
  const result = PAY_METHODS.map(M => sorted.find(S => S.p23_bmd_method === M))
    .filter(isDefined)
    .map((M: typeInfo.p27_collect_sequence) => ({
      id: M.p27_cse_p23_bmd_id,
      method: M.p23_bmd_method,
      label: `CasinoCore.diposit_method_${M.p23_bmd_method}`,
      sort: M.p27_cse_sequence,
      max: M.p23_bmd_singular_upper_limit,
      min: M.p23_bmd_singular_lower_limit,
    }));
  return result;
}

/** 解除 typescript 認為 filter 無法排除 undefined 的窘境 */
function isDefined<T>(argument: T | undefined): argument is T {
  return argument !== undefined;
}
