/* eslint-disable no-param-reassign */
import Info, {
  typeInfo,
  setMethods,
} from "../../service/casinoPaymentFlow/collectSequence/info";
import Create from "../../service/casinoPaymentFlow/stored/create";
import create, {
  typeCreate,
} from "../../service/casinoPaymentFlow/stored/create";

type state = {
  data: typeInfo.p27_collect_sequence[];
  list: typeInfo.filtedItem[];
  /** 結帳資訊 */
  payForm?: typeCreate.response;
};

const state: state = {
  data: [],
  list: [],
  payForm: {
    code: "MyCash",
    result: {
      url: "https://api.mycash.asia/payment/VirAccountPaymentGate.php",
      form_data: {
        HashKey: "Y9WGVRFTUVRFA5SAJN3CAE55T",
        HashIV: "AW7CBUAYTHLBP8WBP5BJMCS36V",
        MerTradeID: "46443081-94a7-4bed-8919-0dc2f8af628e",
        MerProductID: "0001",
        MerUserID: "4644308194a74bed89190dc2f8af628e",
        Amount: 100,
        TradeDesc:
          "\u6b64\u70ba\u5546\u57ce\u865b\u64ec\u5546\u54c1\u8cfc\u8cb7\uff0c\u4e26\u975e\u5be6\u9ad4\u5546\u54c1\u4ea4\u6613\uff0c\u8acb\u52ff\u53d7\u9a19\u4e0a\u7576\uff0c\u4e26\u8acb\u52ff\u4ee3\u4ed6\u4eba\u7e73\u6b3e\u5132\u503c\uff0c\u5c0f\u5fc3\u89f8\u6cd5",
        ItemName: "\u5546\u57ce\u5546\u54c1",
      },
    },
  },
};

export default {
  namespaced: true,
  state,
  mutations: {
    setList: (st: state, data: typeInfo.response) => {
      const list = data.p27_collect_sequence;
      st.data = list;
      /** TODO: sort, getuniq , return only 6 paymethods {methodname,max,min} */
      st.list = setMethods(list);
    },
    setPayForm: (st: state, data: typeCreate.response) => {
      // TODO: 交易類型的判斷，回傳指定的component供交易用
      st.payForm = data;
    },
  },
  actions: {
    async getInfo({ commit }: any) {
      const res = await (await Info()).data.response;
      commit("setList", res);
    },
    async create({ commit }: any, req: typeCreate.request) {
      const res = await (await Create(req)).data.response;
      commit("setPayForm", res);
    },
  },
  getters: {
    list(st: state) {
      return st.list;
    },
    payForm(st: state) {
      return st.payForm;
    },
  },
};
